"use client"

import React from "react"
import { TextBlock, ImageBlock, VideoBlock, QuizBlock, EditorBlock } from "@/lib/scorm/types"
import TextPanel from "./panels/text-panel"

interface Props {
  selectedBlock: EditorBlock | null
  onBlockChange: (block: EditorBlock) => void
}

export function PropertiesPanel({ selectedBlock, onBlockChange }: Props) {
  if (!selectedBlock) {
    return (
      <div className="p-4 text-center text-slate-400 text-sm">
        Select a block to edit its settings.
      </div>
    )
  }

  // Render text block panel
  if (selectedBlock.type === "text") {
    return (
      <TextPanel
        block={selectedBlock as TextBlock}
        onChange={(b) => onBlockChange(b)}
      />
    )
  }

  // TEMP placeholder for image blocks
  if (selectedBlock.type === "image") {
    return (
      <div className="p-4 text-sm">
        <p className="font-semibold mb-2">Image Settings</p>

        <label className="block mb-1 text-xs">Image URL</label>
        <input
          type="text"
          className="w-full border rounded px-2 py-1"
          value={(selectedBlock as ImageBlock).src}
          onChange={(e) =>
            onBlockChange({ ...selectedBlock, src: e.target.value })
          }
        />

        <label className="block mt-3 mb-1 text-xs">Alt text</label>
        <input
          type="text"
          className="w-full border rounded px-2 py-1"
          value={(selectedBlock as ImageBlock).alt ?? ""}
          onChange={(e) =>
            onBlockChange({ ...selectedBlock, alt: e.target.value })
          }
        />
      </div>
    )
  }

  // TEMP placeholder for video blocks
  if (selectedBlock.type === "video") {
    return (
      <div className="p-4 text-sm">
        <p className="font-semibold mb-2">Video Settings</p>

        <label className="block mb-1 text-xs">Video URL</label>
        <input
          className="w-full border rounded px-2 py-1"
          type="text"
          value={(selectedBlock as VideoBlock).src}
          onChange={(e) =>
            onBlockChange({ ...selectedBlock, src: e.target.value })
          }
        />
      </div>
    )
  }

  // TEMP placeholder for quiz blocks
  if (selectedBlock.type === "quiz") {
    const quiz = selectedBlock as QuizBlock

    return (
      <div className="p-4 text-sm">
        <p className="font-semibold mb-2">Quiz Settings</p>

        <label className="block mb-1 text-xs">Question</label>
        <input
          className="w-full border rounded px-2 py-1"
          type="text"
          value={quiz.question}
          onChange={(e) =>
            onBlockChange({ ...quiz, question: e.target.value })
          }
        />

        <p className="mt-3 font-medium text-xs">Options</p>

        {quiz.options.map((o, i) => (
          <div key={o.id} className="flex items-center gap-2 mt-2">
            <input
              className="border rounded px-2 py-1 flex-1"
              type="text"
              value={o.label}
              onChange={(e) => {
                const updated = [...quiz.options]
                updated[i] = { ...o, label: e.target.value }
                onBlockChange({ ...quiz, options: updated })
              }}
            />
          </div>
        ))}
      </div>
    )
  }

  // If type is unknown
  return (
    <div className="p-4 text-center text-slate-400 text-sm">
      No editor available for this block type.
    </div>
  )
}
